<template>
  <div :class="`circle fs theme__${theme | '0'}`">
    <div :class="`inner-circle ${isUp ? 'up' : 'down'}`" :style="innerStyle">
      <div class="title">{{ title }}</div>
      <div class="total">{{ data.total }}</div>
      <div class="ratio flex-center">
        <div class="ratio__wrapper flex">
          <div class="label">同比</div>
          <div class="icon">
            <img v-if="isUp" src="/widgets/relative_ratio_circle/up.png" />
            <img v-else src="/widgets/relative_ratio_circle/down.png" />
          </div>
          <div class="percent">{{ percent }}</div>
        </div>
      </div>
      <DataIncFlash
        v-if="flash"
        :data="data.total"
        font-size="16px"
        :flash-style="{ top: '-20px', left: '50px' }"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["data", "title", "size", "theme", "flash"],

  data() {
    return {
      chart: null,
    };
  },

  computed: {
    innerStyle() {
      return {
        width: this.size || "100%",
        height: this.size || "100%",
      };
    },

    isUp() {
      return this.data.inc >= 0;
    },

    percent() {
      const data = this.data;
      const percent = this.$percentage(data.inc / data.total);
      return `${Math.abs(percent)}%`;
    },
  },

  mounted() {},

  methods: {},
};
</script>

