import { request } from "@/lib/http";

let testCounter = 0;

/** 加载阶段数据 */
export async function loadDailyData(pageParams, pageData, force) {
  if (!pageParams || !pageData) return;

  const { scopeType, periodType } = pageParams || {};
  if (!scopeType) return;

  if (!pageData[scopeType]) {
    pageData[scopeType] = pageData[scopeType] || {};
  }

  const scopeData = pageData[scopeType];

  if (!scopeData.daily) {
    scopeData.daily = {};
  }

  testCounter++;

  const url = testCounter % 2 ? "/daily2.json" : "/daily.json";

  const res = await request(url, pageParams);

  scopeData.daily = res;

  const data = scopeData.daily;

  return data;
}

