<template>
  <div ref="chart" class="rank-bar-chart fs"></div>
</template>

<script>
import { graphic } from "echarts";

export default {
  props: ["data", "unit", "chartOptions"],

  data() {
    return {
      chart: null,
    };
  },

  computed: {
    innerChartOptions() {
      // 由大到小排列（倒序）
      const data = (this.data || []).reverse();

      const nameData = data.map((it) => it.name);
      const valueData = data.map((it) => it.value);

      return {
        grid: {
          left: -30,
          right: 70,
          top: "3%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          show: false,
          type: "value",
        },
        yAxis: {
          type: "category",
          data: nameData,
          axisLine: { show: false },
          axisTick: { show: false },
          axisLabel: {
            color: "#fff",
            margin: 100,
            width: 200,
            overflow: "truncate",
            fontWeight: "bold",
            align: "left",
            formatter: (params, index) => {
              return `{a|${data.length - index}.}  ${params}`;
            },
            align: "left",
            rich: {
              a: {
                color: "#f0ba37",
                fontWeight: "bold",
              },
            },
            ...this.chartOptions?.yAxisLabel,
          },
        },
        series: [
          {
            name: "rank",
            type: "bar",
            barWidth: 12,
            label: {
              show: true,
              position: "right",
              valueAnimation: true,
              formatter: `  {c} ${this.unit || "次"}`,
              textStyle: {
                color: "#2a89fe",
                fontWeight: "bold",
              },
            },
            itemStyle: {
              normal: {
                barBorderRadius: [10, 10, 10, 10],
                color: new graphic.LinearGradient(0, 0, 1, 0, [
                  { offset: 0, color: "#D6620B" },
                  { offset: 1, color: "#F1BC38" },
                ]),
              },
            },
            data: valueData,
            ...this.chartOptions?.series,
          },
        ],
        ...this.chartOptions?.option,
      };
    },
  },

  watch: {
    data(cur, old) {
      if (this.$deepEquals(cur, old)) return;

      this.load();
    },
  },

  mounted() {
    this.load();
  },

  methods: {
    load() {
      this.loadChart();
    },

    loadChart() {
      if (!this.chart && this.$refs.chart) {
        this.chart = this.$echarts(this.$refs.chart);
      }

      if (!this.chart) return;

      this.chart.clear();
      this.chart.resize();

      this.chart.setOption(this.innerChartOptions);
    },
  },
};
</script>

<style lang="less" scoped>
.rank-bar-chart {
}
</style>
