import { mapState, mapActions } from "pinia";
import { useDashboardStore } from "@/store/dashboard";

import BlockSection from "@/components/dashboard/section/BlockSection.vue";
import DataSectionHeader from "@/components/dashboard/reports/shared/DataSectionHeader.vue";
import ScrollListPeriod from "@/components/dashboard/reports/shared/ScrollListPeriod.vue";
import ScrollListBlockDaily from "@/components/dashboard/reports/shared/ScrollListBlockDaily.vue";
import SimpleBarChartDaily from "@/components/dashboard/reports/shared/SimpleBarChartDaily.vue";
import RelativeRatioCircle from "@/components/dashboard/reports/shared/RelativeRatioCircle.vue";

const commonMixin = {
  computed: {
    // 范围类型
    scopeType() {
      return this.pageParams.scopeType;
    },

    // 阶段类型
    periodType() {
      return this.pageParams.periodType;
    },

    // 阶段名称
    periodName() {
      let name = "";

      if (this.isMonthly) {
        name = "本月";
      } else if (this.isAnnual) {
        name = "本年";
      }

      return name;
    },

    // 是否极端
    isGroup() {
      return this.scopeType == "group";
    },

    // 是否企业
    isCompany() {
      return this.scopeType == "company";
    },

    // 是否系统
    isSystem() {
      return this.scopeType == "system";
    },

    // 是否月度
    isMonthly() {
      return this.periodType == "monthly";
    },

    // 是否年度
    isAnnual() {
      return this.periodType == "annual";
    },

    // 范围数据
    scopeData() {
      return this.pageData[this.scopeType] || {};
    },

    // 阶段数据
    periodData() {
      return this.scopeData[this.periodType] || {};
    },
  },
};

/** 看板mixin */
export const dashboardMixin = {
  ...commonMixin,

  computed: {
    ...commonMixin.computed,
    ...mapState(useDashboardStore, ["pageParams", "pageData"]),
  },

  methods: {
    ...mapActions(useDashboardStore, ["loadData"]),
  },
};

/** 看板块mixin */
export const dashboardBlockMixin = (dataProp) => {
  return {
    ...commonMixin,

    components: {
      BlockSection,
      DataSectionHeader,
      ScrollListPeriod,
      ScrollListBlockDaily,
      SimpleBarChartDaily,
      RelativeRatioCircle,
    },

    data() {
      return {
        chart: null,
      };
    },

    computed: {
      ...commonMixin.computed,
      ...mapState(useDashboardStore, ["pageParams", "pageData"]),

      data() {
        return this.periodData[dataProp] || {};
      },
    },

    watch: {
      data(cur, old) {
        if (this.$deepEquals(cur, old)) return;

        this.load();
      },
    },

    mounted() {
      this.load();
    },

    methods: {
      load() {
        this.loadChart();
      },

      loadChart() {
        if (!this.chart && this.$refs.chart) {
          this.chart = this.$echarts(this.$refs.chart);
        }

        if (!this.chart) return;

        this.chart.clear();
        this.chart.resize();

        if (this.chartOptions) {
          this.chart.setOption(this.chartOptions);
        }
      },
    },
  };
};
