<template>
  <div ref="chart" class="simple-bar-chart-daily fs"></div>
</template>

<script>
import { graphic } from "echarts";

export default {
  props: ["data", "unit", "chartOptions"],

  data() {
    return {
      chart: null,
    };
  },

  computed: {
    innerChartOptions() {
      // 由大到小排列（倒序）
      const data = this.data || [];

      const nameData = data.map((it) => it.name);
      const valueData = data.map((it) => it.value);

      return {
        grid: {
          left: 0,
          right: 30,
          top: "3%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          axisLabel: {
            color: "#b4e4ff",
          },
          axisLine: {
            show: true,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
          data: nameData,
        },
        yAxis: {
          axisLabel: {
            color: "#b4e4ff",
          },
          axisLine: {
            show: true,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
          splitLine: {
            show: true,
            interval: 100,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
        },
        series: [
          {
            type: "bar",
            barWidth: 10,
            data: valueData,
            itemStyle: {
              color: new graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "#13d2d2" },
                { offset: 1, color: "#2f54eb" },
              ]),
            },
            ...this.chartOptions?.series,
          },
        ],
        ...this.chartOptions?.option,
      };
    },
  },

  watch: {
    data(cur, old) {
      if (this.$deepEquals(cur, old)) return;

      this.load();
    },
  },

  mounted() {
    this.load();
  },

  methods: {
    load() {
      this.loadChart();
    },

    loadChart() {
      if (!this.chart && this.$refs.chart) {
        this.chart = this.$echarts(this.$refs.chart);
      }

      if (!this.chart) return;

      this.chart.clear();
      this.chart.resize();

      this.chart.setOption(this.innerChartOptions);
    },
  },
};
</script>

<style lang="less" scoped>
.simple-bar-chart-daily {
}
</style>
