<script>
const elLinks = {
  "#u61": { path: "/dashboard/group/general" },
  "#u69": { path: "/dashboard/company/general" },
  "#u77": { path: "/dashboard/system/general" },
};

export default {
  name: "",
  data() {
    return {
      links: {},
    };
  },

  mounted() {
    this.initLinks();
  },

  methods: {
    initLinks() {
      Object.keys(elLinks).forEach((key) => {
        let el = this.$refs.base.querySelector(key);

        if (el) {
          el.onclick = () => {
            this.$router.push(elLinks[key].path);
          };
        }
      });
    },
  },
};
</script>

