<template>
  <div
    :class="`block-section section theme-${theme || '0'}`"
    :style="sectionStyle"
  >
    <div v-if="$slots.header || title" class="section__header">
      <slot name="header">
        <div v-if="title" class="title">
          <div v-if="theme == '2'" class="icon">
            <img src="/widgets/section/header_arrow.png" />
          </div>
          <div class="text">{{ title }}</div>
        </div>
      </slot>
    </div>
    <div :class="`section__body ${isHeader ? '' : 'fs'}`">
      <slot />
    </div>

    <div class="corner corner__tl">&nbsp;</div>
    <div class="corner corner__tr">&nbsp;</div>
    <div class="corner corner__bl">&nbsp;</div>
    <div class="corner corner__br">&nbsp;</div>
  </div>
</template>

<script>
export default {
  props: ["title", "width", "height", "theme"],

  data() {
    return {};
  },

  computed: {
    isHeader() {
      return !!this.$slots.header || !!this.title;
    },

    sectionStyle() {
      return {
        width: this.width || "100%",
        height: this.height || "auto",
      };
    },
  },

  mounted() {},
  beforeDestroy() {},
};
</script>

