import { defineStore } from "pinia";
import * as dateUtil from "@/lib/date";

import * as dataLoader from "./loader";

export const useDashboardStore = defineStore("dashboard", {
  state: () => ({
    pageParams: {
      scopeType: "",
      periodType: "",
      month: null,
      year: null,
    },
    pageData: {
      group: {
        general: null,
        daily: null,
        monthly: null,
        annual: null,
      },
      company: {
        general: null,
        daily: null,
        monthly: null,
        annual: null,
      },
      system: {
        general: null,
        daily: null,
        monthly: null,
        annual: null,
      },
    },
  }),

  getters: {},

  actions: {
    async loadData(params, force) {
      params = { ...params };

      this.pageParams = {
        ...this.pageParams,
        ...params,
        month: params.month || this.pageParams.month || dateUtil.getMonth(),
        year: params.year || this.pageParams.year || dateUtil.getYear(),
      };

      const { scopeType, periodType } = this.pageParams || {};

      if (!scopeType || !periodType) return;

      if (!this.pageData[scopeType]) {
        this.pageData[scopeType] = {};
      }

      let res = null;

      switch (periodType) {
        case "general":
          res = await dataLoader.loadGeneralData(
            this.pageParams,
            this.pageData,
            force
          );
          break;
        case "monthly":
        case "annual":
          res = await dataLoader.loadPeriodData(
            this.pageParams,
            this.pageData,
            force
          );
          break;
        case "daily":
          res = await dataLoader.loadDailyData(
            this.pageParams,
            this.pageData,
            force
          );
          break;
      }

      if (!res) return;

      this.pageData[scopeType][periodType] = res;
    },
  },
});
