import Vue from "vue";
import Router from "vue-router";
import main from "@/views/main";
import dashboard from "@/views/dashboard/dashboard";

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/main",
    },
    {
      path: "/main",
      name: "main",
      component: main,
    },
    {
      path: "/dashboard",
      name: "dashboard",
      component: dashboard,
      children: [
        {
          path: ":scopeType/general",
          component: () => import("@/views/dashboard/general/index"),
          meta: { loadData: true, periodType: "general" },
        },
        {
          path: ":scopeType/daily",
          component: () => import("@/views/dashboard/daily/index"),
          meta: { loadData: true, periodType: "daily" },
        },
        {
          path: ":scopeType/:periodType",
          component: () => import("@/views/dashboard/period/index"),
          meta: { loadData: true },
        },
      ],
    },
  ],
});

export default router;
