<template>
  <vue-seamless-scroll :data="innerData" :class-option="classOption">
    <slot />
  </vue-seamless-scroll>
</template>

<script>
import VueSeamlessScroll from "vue-seamless-scroll";
export default {
  components: { VueSeamlessScroll },

  props: ["data", "limitMoveNum"],

  data() {
    return {};
  },

  computed: {
    innerData() {
      return this.data || [];
    },

    classOption() {
      return {
        limitMoveNum: this.limitMoveNum,
      };
    },
  },

  mounted() {},
  beforeDestroy() {},
};
</script>

<style lang="less" scoped></style>
