import axios from "axios";

import { env } from "@/env";

axios.defaults.withCredentials = true;

/** 请求接口 */
export async function request(url, params) {
  const baseUrl = env.baseUrl || "";

  try {
    const resp = await axios({
      method: "get",
      url: baseUrl + url,
      data: params,
    });

    const res = resp.data || {};

    if (resp.status == 200) {
      return res;
    } else {
      throw new Error(res.message);
    }
  } catch (err) {
    console.error(err);
  }
}
