<template>
  <div class="dashboard-frame frame">
    <div class="frame__header">
      <div class="title">
        <div class="top">&nbsp;</div>
        <img src="/pages/dashboard/frame/header_bg.png" />
        <div class="text">业财融合数据看板</div>
      </div>
      <div class="menus">
        <div class="menus__wrapper">
          <template v-for="it in menus">
            <div
              v-if="it.pos === 'left'"
              :class="`menu__wrapper`"
              :key="it.name"
            >
              <a
                :class="`menu__item left ${getSelectedClass(it)}`"
                @click="handleMenuClick(it)"
              >
                <div class="name">{{ it.name }}</div>
              </a>
            </div>
          </template>
        </div>
        <div class="menus__placeholder">&nbsp;</div>
        <div class="menus__wrapper">
          <template v-for="it in menus">
            <div
              v-if="it.pos === 'right'"
              :class="`menu__wrapper`"
              :key="it.name"
            >
              <a
                :class="`menu__item right ${getSelectedClass(it)}`"
                @click="handleMenuClick(it)"
              >
                <div class="name">{{ it.name }}</div>
              </a>
            </div>
          </template>
        </div>
      </div>
      <div class="clock">
        <div class="clock__wrapper">
          <div class="date">{{ date }}</div>
          <div class="time">{{ time }}</div>
        </div>
      </div>
    </div>

    <div class="frame__body">
      <slot />
    </div>
  </div>
</template>

<script>
import { mapActions } from "pinia";

import { useDashboardStore } from "@/store";

import { dashboardMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  name: "",

  mixins: [dashboardMixin],

  data() {
    return {
      datetime: new Date(),
      ticker: null,
      dailyCounter: 0,
    };
  },

  computed: {
    date() {
      const dt = this.datetime;

      const year = dt.getFullYear();
      const month = dt.getMonth() + 1;
      const date = dt.getDate();

      return `${year}-${month}-${date}`;
    },
    time() {
      const dt = this.datetime;

      const hours = dt.getHours();
      const minutes = dt.getMinutes();
      const seconds = dt.getSeconds() + 1;

      return `${hours} : ${minutes} : ${seconds}`;
    },

    menus() {
      return [
        { name: "首页", path: "/main", pos: "left" },
        { name: "总览", path: "general", pos: "left" },
        { name: "24小时", path: "daily", pos: "left" },
        { name: "月度", path: "monthly", pos: "right" },
        { name: "年度", path: "annual", pos: "right" },
      ];
    },
  },

  mounted() {
    this.ticker = setInterval(() => {
      this.datetime = new Date();

      this.handleDailyCounter();
    }, 1000);
  },

  beforeDestroy() {
    if (this.ticker) {
      clearInterval(this.ticker);
      this.ticker = null;
    }
  },

  methods: {
    ...mapActions(useDashboardStore, ["loadData"]),

    handleMenuClick(menu) {
      let path = menu.path;

      if (!path) return;

      if (!path.startsWith("/")) {
        path = `/dashboard/${this.scopeType}/${path}`;
      }

      this.$router.push(path);
    },

    /** 每10秒请求一次接口 */
    handleDailyCounter() {
      this.dailyCounter++;

      if (this.dailyCounter < 5) return;

      this.dailyCounter = 0;

      // 加载每日数据
      this.loadData();
    },

    getSelectedClass(menu) {
      if (!menu?.path) return "";

      if (menu.path === this.$route.path) return "selected";
      if (this.periodType === menu.path) return "selected";
      return "";
    },
  },
};
</script>

