<template>
  <div :class="`data-tray theme-${theme || '0'}`" :style="innerStyle">
    <div class="section__header row">
      <div class="title">{{ title }}</div>

      <div class="total">{{ data.total }}</div>

      <DataIncFlash
        v-if="flash"
        :data="data.total"
        font-size="20px"
        :flash-style="{ top: '-40px', right: '50px' }"
      />
    </div>
    <div class="section__body">
      <div class="list row">
        <div class="list__item" v-for="it in dataList" :key="it.name">
          <div class="tray">
            <img :src="`/widgets/data_tray/tray_${it.trayIndex}.png`" />
          </div>
          <div :class="`content tray_${it.trayIndex}`">
            <DataIncFlash
              v-if="flash"
              :data="it.value"
              font-size="20px"
              :flash-style="{ top: '-10px', right: '0px' }"
            />
            <div class="wrapper">
              <div class="name">{{ it.name }}</div>
              <div class="value">{{ it.value }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["title", "data", "height", "width", "theme", "flash"],

  data() {
    return {};
  },

  computed: {
    innerStyle() {
      return {
        width: this.width || "100%",
        height: this.height || "auto",
      };
    },

    dataList() {
      return (this.data.list || []).map((it, index) => {
        return { ...it, trayIndex: (index % 3) + 1 };
      });
    },
  },

  mounted() {},
};
</script>

