<template>
  <div class="data-inc-flash">
    <div ref="flash" class="flash" :style="innerFlashStyle">
      <div class="inc">{{ incStr }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Number,
      default: 0,
    },

    fontSize: {
      type: String,
      default: "16px",
    },

    flashStyle: {
      type: Object,
    },
  },

  data() {
    return {
      oldData: 0,
      flashing: false,
      flash: null,
      inc: 0,
      queue: [],
    };
  },

  computed: {
    incStr() {
      const inc = this.inc;
      if (inc > 0) {
        return `+${inc}`;
      } else if (inc < 0) {
        return `${inc}`;
      }
    },

    innerFlashStyle() {
      return {
        fontSize: this.fontSize,
        ...this.flashStyle,
      };
    },
  },

  watch: {
    data(cur, old) {
      const flash = this.$refs.flash;
      if (!flash) return;

      // cur = (cur || 0) + parseInt(Math.random() * 100);  // mock数据
      cur = cur || 0;
      old = old || 0;

      if (this.flashing) return; // 正在闪烁则不进行调整

      const inc = cur - (this.oldData || old);
      if (!inc) return;

      this.flashing = true;

      flash.style.display = "block";
      flash.classList.remove("fadeout");
      flash.classList.add("fadein");

      this.inc = inc;

      setTimeout(() => {
        flash.classList.remove("fadein");
        flash.classList.add("fadeout");

        setTimeout(() => {
          flash.style.display = "none";
          this.oldData = cur; // 记录最新值
          this.flashing = false;
        }, 3000);
      }, 3000);
    },
  },

  mounted() {},
};
</script>

<style lang="less" scoped>
.data-inc-flash {
  position: relative;
  height: 0;

  .flash {
    position: absolute;
    color: #00e66a;

    .inc {
      white-space: nowrap;
    }
  }

  .fadeout {
    animation: fadeout 3s;
  }

  .fadein {
    animation: fadein 3s;
  }
}

/* 移出动画 */
@keyframes fadeout {
  0% {
    opacity: 1;
    display: block;
  }
  100% {
    opacity: 0;
    display: block;
  }
}

/* 进入动画 */
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>
