const TestEnv = {
  baseUrl: "",
};

const ProdEnv = {
  baseUrl: "",
};

const envMap = {
  dev: {
    // 开发/本地环境
    HOSTs: ["127.0.0.1", "localhost"],
    ENV: { baseUrl: "/data" },
  },

  test: {
    // 测试环境
    HOSTs: ["1.15.128.83"],
    ENV: { ...TestEnv },
  },

  pre: {
    // 预发布环境
    HOSTs: [],
    ENV: { ...ProdEnv },
  },

  prod: {
    // 生产环境
    HOSTs: [],
    ENV: { ...ProdEnv },
  },
};

export const env = getCurrentEnvFromEnvMap(envMap);

/** 获取当前环境变量 */
function getCurrentEnvFromEnvMap(envMap, defaultValue = true) {
  const envNames = Object.keys(envMap);

  let envName = envNames.find((key) => {
    const _env = envMap[key];
    return _env && _env.HOSTs && _env.HOSTs.some((h) => checkHrefHost(h));
  });

  if (!envName) {
    if (defaultValue === false) return null;
    envName = envNames[0]; // 默认环境
  }

  const env = (envMap[envName] || {}).ENV;

  return { name: envName, ...env };
}

/** 检查当前href是否为指定的host */
function checkHrefHost(host) {
  const href = window.location.href;
  const hostIndex = href.indexOf(host);

  if (
    hostIndex === 0 ||
    ["https://", "http://"].includes(href.substring(0, hostIndex))
  )
    return true;

  return false;
}
