<template>
  <div class="dashboard-module module" style="height: 100%">
    <DashboardFrame>
      <router-view v-if="flag" :selectRangeDate="selectRangeDate"></router-view>
    </DashboardFrame>
  </div>
</template>

<script>
import { mapActions } from "pinia";
import { useDashboardStore } from "@/store/dashboard";

import DashboardFrame from "@/components/dashboard/frame/DashboardFrame2.vue";

export default {
  name: "",
  components: {
    DashboardFrame,
  },
  data() {
    return {
      flag: true,
      selectRangeDate: [],
    };
  },
  watch: {
    $route: {
      handler() {
        this.loadPageData();
      },

      immediate: true,
    },
  },

  methods: {
    ...mapActions(useDashboardStore, ["loadData"]),

    loadPageData() {
      const { params, meta, path, query } = this.$route;

      const isLoadData = meta.loadData;
      if (!isLoadData || !path.startsWith("/dashboard/")) return;

      const scopeType = params.scopeType;
      const periodType = params.periodType || meta.periodType;

      if (!scopeType || !periodType) return;

      this.loadData({
        scopeType,
        periodType,
        month: parseInt(query.month) || null,
        year: parseInt(query.year) || null,
      });
    },
  },
};
</script>

<style lang="less" scoped>
.dashboard-module {
  background: url(/pages/dashboard/bg.png);
}
</style>
