<template>
  <div class="progress-chart">
    <div ref="chart" class="inner-chart"></div>
  </div>
</template>

<script>
export default {
  props: ["data"],

  data() {
    return {
      myChart: null,
    };
  },

  computed: {
    percentage() {
      let { participated, notApplied } = this.data || {};

      participated = participated || 0;
      notApplied = notApplied || 0;

      if (!participated || !notApplied) return "--";

      return this.$percentage(participated / (participated + notApplied));
    },
  },

  watch: {
    data(cur, old) {
      if (!this.$deepEquals(cur, old)) return;

      this.load();
    },
  },

  mounted() {
    setTimeout(() => {
      this.load();
    }, 1000);
  },

  methods: {
    load() {
      if (!this.myChart) this.myChart = this.$echarts(this.$refs.chart);

      this.myChart.clear();
      this.myChart.resize();

      this.myChart.setOption({
        color: ["#128AF9", "#6A6B6D"],
        title: {
          text: this.percentage + "%", //主标题文本
          left: "center",
          top: "35%",
          textStyle: {
            fontSize: 28,
            color: "#B4E4FF",
            align: "center",
          },
        },
        series: [
          {
            type: "pie",
            radius: ["75%", "100%"],
            avoidLabelOverlap: false,
            label: { show: false, position: "center" },
            emphasis: {
              label: { show: false },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: this.data?.participated, name: "participated" },
              { value: this.data?.notApplied, name: "notApplied" },
            ],
          },
        ],
      });
    },
  },
};
</script>

<style lang="less" scoped>
.progress-chart {
  border: 1px solid #123f73;
  padding: 7px;
  border-radius: 50%;
  width: 146px;
  height: 146px;
}

.inner-chart {
  width: 132px;
  height: 132px;
}
</style>
